h1{
    font-size: 4.8rem;
}
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.app{
    width: 100%;
    height: 92vh;
    position: relative;
    color: white;
    
}

.app::before{
    content: '';
    background: url('./Components/background1.jpg') no-repeat center center/cover;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.container{
    margin-left: 20px;
    
}

.bottom{
    width: 370px;
    height: 70px;
    margin: 1rem auto;
    /* padding: 0 1rem; */
    position: absolute;
    top: 530px;
    left: 580px;
    display: flex;
    justify-content: space-evenly;
    border-radius: 10px;
    background-color: rgba(255, 255, 255,0.2);
    
    
}

.top{
    position: relative;
    top: 100px;
}

.description{
    position: relative;
    top: 100px;
    font-size: 1.8rem;
}

.app .search{
    text-align: center;
    padding: 1rem;
}

.app input{
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 25px;
    border: 1px solid rgba(255, 255, 255,0.8);
    background: rgba(255, 255, 255,0.1);
    color: white;
}

::placeholder{
    color: white;
}